import addr from './addreses';
import strings from './../shared/constant';

export default class storeApi {
    static async getSingleStore(id) {
        let url = `${addr.getSingleStore}?id=${id}`;
        try {
            const response = await fetch(url, {
                method: 'GET',
                mode: 'cors',
                headers: {
                    'Content-Type': 'application/json; charset=utf-8;'
                }
            });
            const rep = await response.json();
            if (!rep.IsSuccessful) return {
                success: false,
                message: rep.Message
            }

            else return {
                success: true,
                result: {
                    name: rep.Result.Name,
                    logoUrl: rep.Result.LogoUrl
                }
            }

        }
        catch (error) {
            console.log('got error');
            if ('caches' in window)
                caches.match(url).then(function (data) {
                    console.log('data');
                    console.log(data);
                    if (data) return {
                        success: true,
                        result: {
                            name: data.Result.Name,
                            logoUrl: data.Result.LogoUrl
                        }
                    };
                    else return ({ success: false, message: strings.connecttionFailed });
                });
        }

    }
}